        <h2>Introduction</h2>
        <p class="intro">The process to develop a new Academic and Strategic Plan was launched in September 2021 as the current Academic and Strategic plans were ending in 2022. Developing this integrated plan was a collaborative initiative led by the Academic Planning and Strategic Planning committees.</p>
        <p>The previous <a href="https://www.ocadu.ca/Assets/content/governance/Academic-Plan-2017.pdf">Academic Plan (2017-2022)</a> presented the guiding principles of decolonization, diversity and equity, sustainability, valuing faculty, interdisciplinarity, and health and wellness. In looking to the future, we believed these principles continued to be important and should be honoured in the process of developing a new Academic and Strategic Plan.</p>
        <p>As a result, we designed our planning approach to embody and express these guiding principles, identified in the core design principles found below.</p>

        <h3 ref="core-design" class="anchor">Core design principles</h3>
        <h4>Co-creative</h4>
        <p>Reflecting the collective intelligence of such a diverse community with many disciplines required us to shift each step of engagement from extractive consultation to creative co-development. This meant that at each stage, stakeholders were asked to imagine and create the solutions that would excite them.</p>
        <h4>Open and transparent</h4>
        <p>To honour the creations, efforts, energy and inputs of each group that contributed, while also maintaining the agency of other stakeholders throughout the process to add, evolve, debate and adapt, we sought to keep all inputs, steps, assumptions and products visible and available throughout the process so provenance was clear.</p>
        <h4>Iterative</h4>
        <p>This transparency allowed for an iterative process; one in which products of increasing fidelity could be produced and engaged with repeatedly.</p>
        <h4>Context aware</h4>
        <p>By leveraging the perspectives of each stakeholder group and shifting our deliberative lens, we built our plans with a view not only to changes in the outside world and our place within it, but also the views and experiences of our community members.</p>
        <h4>Technology-enabled, but dialogue-driven</h4>
        <p>Multi-stakeholder dialogue was seen as the single, driving force in the process but we used technology to enrich that dialogue through broad data collection, analysis and visualization. This enabled us to find points of contention, convergence and to assist in pattern recognition and transparency. Visual repositories, survey tools, data graphing and deliberative wiki-surveys all helped to keep the dialogues connected, inclusive and informed.</p>

        <h3 ref="process" class="anchor">Process</h3>
        <!-- <p>The previous Academic Plan (2017-2022) presented the guiding principles of decolonization, diversity and equity, sustainability, valuing faculty, interdisciplinarity, and health and wellness. In looking to the future, we believed these principles continued to be important and should be honoured in the process of developing a new Academic and Strategic Plan.</p> -->
        <p>The combined Academic and Strategic Planning process began in September of 2021 with a questionnaire sent to all members of the University community asking for input on both the priorities of the community, and the issues and context they felt a plan should consider.</p>
        <p>The results of the questionnaire were analyzed and categorized, then prioritized through voting by the Academic and Strategic Planning committees. Through a series of collaborative design sessions held in the fall of 2021, the committees imagined ways of addressing the emerging priorities and discussed issues they felt the University could impact through its collective work.</p>
        <p>As priority areas emerged from these early collaborative sessions, stakeholder groups across the University community were engaged to further develop and contribute to the thematic areas, following the principle of designing <em>with</em> instead of <em>for</em> groups within the University. </p>
        <p>By December 2021, with a stable set of priorities having been developed, working groups pushed them to greater detail, and they were challenged, stress tested and iterated through an extensive round of collaborative sessions. </p>
        <p>In order to translate the priorities into measurable objectives, unit heads across the University were polled to ascertain where work and energy were currently aligning with the priorities of the plan. This was then evaluated through a series of dialogues with the Board of Governors, Senate and Committees to accommodate their visions for those priority areas.</p>
        <p>The final plan expressed a shared Vision and Mission as well as a set of Priorities. These were presented for approval to governing bodies who had been invited to contribute at each stage in the process of the plan’s development.</p>
      
        <h3 ref="format" class="anchor">Format of consultations</h3>
